import React, { Component } from 'react'

export class OneBtnComponent extends Component {
  render() {
    return (
        <div className="w3-third w3-margin-bottom">        
        <div className="w3-container w3-white">
          <p><b>{this.props.Titel}</b></p>
          <p className="w3-opacity">{this.props.SoldPrice}</p>
          <p>{this.props.Descript}</p>
          <button 
          className="w3-button w3-black w3-margin-bottom"
          onClick={this.props.ClickFun}
          >{this.props.BtnTxt}</button>
        </div>
      </div>
    )
  }
}

export default OneBtnComponent
