import React, { Component } from 'react'
import Web3 from 'web3'
import {mint,isSaleActive,totalSupply,approve,mintToOther,flipSaleActive,withdraw,_isRevealed,balanceOf,MAX_SUPPLY,setMaxBalance,max_Balance,_Max_Mint,_setMaxMint,mint_Price,setMintPrice,mintShow,controlShow,flipReveal} from './CallContract';
import ConnectInfoComponent from './ConnectInfoComponent';
import SoldInfoComponent from './SoldInfoComponent';
import OneBtnComponent from './OneBtnComponent';
import TwoInputOneBtnComponent from './TwoInputOneBtnComponent';
import OneInputOneBtnComponent from './OneInputOneBtnComponent';
import AlterInfoComponent from './AlterInfoComponent'
import BackgroundImg from '../image/mintbackground.jpg';


class App extends Component{
  constructor(props){
    super(props);
    this.state={
      account:'0x0',
      owneraccount:'0x0',
      isSaleActive:'',
      isRevealed:'',
      toAddress:'',
      mintPrice:0,
      setmintPrice:0,
      toQuantity:0,
      balanceQuantity:0,
      totalSupply:0,
      MAX_SUPPLY:0,
      Max_Balance:0,
      MAX_Mint:0,
      setMax_Mint:0,
      setMaxBalanceQuantity:0,
      connectShowable:true,
      mintShowable:false,
      controlShowable:false     
    };    
    this.gasfee=450000;
    this.gaslimit='1000000000';
    this.isSaleActive=this.isSaleActive.bind(this);
    this.isRevealed=this.isRevealed.bind(this);
    this.MAXSUPPLY=this.MAXSUPPLY.bind(this);
    this.setMaxBalance=this.setMaxBalance.bind(this);
    this.mint=this.mint.bind(this); 
    this.mintTo=this.mintTo.bind(this);
    this.connect=this.connect.bind(this); 
    this.approve=this.approve.bind(this);
    this.flipReveal=this.flipReveal.bind(this);
    this.flipSaleActive=this.flipSaleActive.bind(this);
    this.handleInputChange=this.handleInputChange.bind(this);
    this.withdraw=this.withdraw.bind(this);
    this.OwnerbalanceOf=this.OwnerbalanceOf.bind(this); 
    this.setMintPrice=this.setMintPrice.bind(this);   
    this.setMaxMint=this.setMaxMint.bind(this);
    this.mintShow=this.mintShow.bind(this);
    this.controlShow=this.controlShow.bind(this);
  }

  async componentWillMount() {
    await this.loadWeb3()
    if(this.state.connectShowable)
    {
    await this.connect();
    }
    if(this.state.mintShowable){
    await this.MAXSUPPLY();
    await this.totalSupply();
    await this.mintPrice();
    }
    if(this.state.controlShowable)
    {
    await this.MaxMint();
    await this.MAXBalance();    
    await this.isRevealed();
    await this.isSaleActive();
    }    
  }

  

  async loadWeb3(){
  if(window.ethereum){
    window.web3=new Web3(window.ethereum)
  }
  else if (window.web3) {
    window.web3 = new Web3(window.web3.currentProvider)
  }
  else {
    this.setState({connectShowable:false});
    window.alert('Non ethereum browser detected. You should consider Metamask!')
  }
  }  
  
  
  async connect() {
    const eth=window.ethereum;
    await eth.request({method:'eth_requestAccounts'})
    .then((accounts)=>{
    this.setState({account:accounts[0]});
    })
    .then(()=>{
      this.connectShowAble();   
    })

    eth.on('accountsChanged',(accounts) => {      
      this.setState({account:accounts[0]})
      this.connectShowAble();
    })
    ;
  }

  connectShowAble(){
    this.mintShow();
    this.controlShow();
  }

 mintShow(){
    const account=this.state.account;
    const result=mintShow(account);
    this.setState({mintShowable:result})
  }

  controlShow(){
    const account=this.state.account;
    const result=controlShow(account);
    this.setState({controlShowable:result})
   }


  async mint(quan) {
    const accounts = this.state.account;
    const quantity=quan;
    const price=this.state.mintPrice*quantity;    
    const mintparameter={
      accountAddress:accounts,
      totalQuantity:quantity,
      price:price,      
      gasfee:this.gasfee,
      gaslimt:this.gaslimit,
    }
    await mint(mintparameter)
  }

  async mintTo() {
    const accounts = this.state.account;         
    const mintToparameter={
      accountAddress:accounts,
      totalQuantity:this.state.toQuantity,
      toAddress:this.state.toAddress,
      gasfee:this.gasfee,
      gaslimt:this.gaslimit,
    }
    await mintToOther(mintToparameter)
  }


  async totalSupply(){    
    const result=await totalSupply();
    this.setState({totalSupply:result})
  }

  async isSaleActive(){    
    const result=await isSaleActive();
    this.setState({isSaleActive:result?'開啟':'關閉'})
  }

  async isRevealed(){    
    const result=await _isRevealed();
    this.setState({isRevealed:result?'開啟':'關閉'})
  }

  async approve(){
    const accounts=this.state.account    
    const approveparameter={
      account:accounts,
      gasfee:this.gasfee,
      gaslimt:this.gaslimit,
    }
    await approve(approveparameter);
  }

  async withdraw(){
    const accounts=this.state.account    
    const approveparameter={
      account:accounts,
      gasfee:this.gasfee,
      gaslimt:this.gaslimit,
    }
    await withdraw(approveparameter);
  }

  async flipReveal(){
    const accounts=this.state.account
    const flipRevealparameter={
      account:accounts,
      gasfee:this.gasfee,
      gaslimt:this.gaslimit,
    }
    await flipReveal(flipRevealparameter);
    await this.isRevealed();
  }

  async flipSaleActive(){
    const accounts=this.state.account    
    const flipSaleActiveparameter={
      account:accounts,
      gasfee:this.gasfee,
      gaslimt:this.gaslimit,
    }    
    await flipSaleActive(flipSaleActiveparameter);
    await this.isSaleActive();    
  }

  async OwnerbalanceOf(){  
    const OwnerbalanceOfparameter={
      owner:this.state.owneraccount 
    }     
    const result=await balanceOf(OwnerbalanceOfparameter);
    this.setState({balanceQuantity:result})
  }

  async mintPrice(){
    const result=await mint_Price();
    this.setState({mintPrice:result})
  }
  async setMintPrice(){
    const accounts=this.state.account    
    const setMintPriceparameter={
      account:accounts,
      price:this.state.setmintPrice,
      gasfee:this.gasfee,
      gaslimt:this.gaslimit,
    }
    await setMintPrice(setMintPriceparameter);
    await this.mintPrice();    
  }


  async MAXSUPPLY(){
    const result=await MAX_SUPPLY();
    this.setState({MAX_SUPPLY:result})
  }

  async MAXBalance(){
    const result=await max_Balance();
    this.setState({Max_Balance:result})
  }

  async setMaxBalance(){
    const accounts=this.state.account    
    const setMaxBalanceparameter={
      account:accounts,
      totalQuantity:this.state.setMaxBalanceQuantity,
      gasfee:this.gasfee,
      gaslimt:this.gaslimit,
    }
    await setMaxBalance(setMaxBalanceparameter);
    await this.MAXBalance();    
  }

  async MaxMint(){
    const result=await _Max_Mint();
    this.setState({MAX_Mint:result})
  }

  async setMaxMint(){
    const accounts=this.state.account    
    const setMaxMintparameter={
      account:accounts,
      totalQuantity:this.state.setMax_Mint,
      gasfee:this.gasfee,
      gaslimt:this.gaslimit,
    }
    await _setMaxMint(setMaxMintparameter);
    await this.MaxMint();    
  }
  

  handleInputChange(e){
    e.preventDefault();
    const ta=e.target;
    const input=ta.value;
    const name=ta.name;    
    this.setState({[name]:input});
  } 


render(){
  return (
    <div className="App">
<div className="w3-content" style={{maxwidth:'2000px',margintop:'46px'}}>


  <div className="w3-black" id="tour" style={{backgroundImage:`url(${BackgroundImg})`}}>
    <div className="w3-container w3-content w3-padding-64" style={{maxwidth:'800px'}}>
    <ConnectInfoComponent Account={this.state.account} ClickFun={this.connect}/>

      <h2 className="w3-wide w3-center">Mint</h2>
      <p className="w3-opacity w3-center"><i>Remember to book your tickets!</i></p><br/>

      {!this.state.mintShowable?
      <AlterInfoComponent Content='Please Connect Your MetaMask'/>
      :
      <div>
      <SoldInfoComponent 
     MaxSupply={this.state.MAX_SUPPLY}
     SoldOutAmount={this.state.totalSupply}
     RemainAmount={this.state.MAX_SUPPLY-this.state.totalSupply}
     />
      <div className="w3-row-padding w3-padding-32" style={{margin:'0-16px'}}>
        <OneBtnComponent
        Titel={'Mint 1個'}
        SoldPrice={'售價：'+this.state.mintPrice*1+ 'ETH'}
        ClickFun={()=>this.mint(1)}
        Descript={'一個剛剛好！'}
        BtnTxt={'Mint'}
        />
        <OneBtnComponent
        Titel={'Mint 5+1個'}
        SoldPrice={'售價：'+this.state.mintPrice*5+ 'ETH'}
        ClickFun={()=>this.mint(5)}
        Descript={'特惠!買五送一!'}
        BtnTxt={'Mint 5+1'}
        />
        <OneBtnComponent
        Titel={'Mint 10+3個'}
        SoldPrice={'售價：'+this.state.mintPrice*10+ 'ETH'}
        ClickFun={()=>this.mint(10)}
        Descript={'特惠!買五送一!'}
        BtnTxt={'Mint 10+3'}
        />       
      </div>
      </div>
      }
    </div>
    {this.state.controlShowable&&
    <div className="w3-container w3-content w3-padding-64" style={{maxwidth:'800px'}}>
    

    <h2 className="w3-wide w3-center">後台操作</h2>           

    <div className="w3-row-padding w3-padding-32" style={{margin:'0-16px'}}>
      <TwoInputOneBtnComponent
      Titel={'空投'}        
      Input1_Descript={'輸入空投地址'}
      Input1_Type={'text'}
      Input1_Name={'toAddress'}        
      Input1_ChangeFun={this.handleInputChange}
      Input2_Descript={'輸入空投數量'}
      Input2_Type={'Number'}
      Input2_Name={'toQuantity'}               
      Input2_ChangeFun={this.handleInputChange}
      ClickFun={this.mintTo}       
      BtnTxt={'空投'}
      />
      <OneInputOneBtnComponent
      Titel={'設定Mint價格'}
      Status={'現在價格'+this.state.mintPrice+'Eth'}
      Input1_Descript={'輸入Mint價格'}
      Input1_Type={'text'}
      Input1_Name={'setmintPrice'}        
      Input1_ChangeFun={this.handleInputChange}
      ClickFun={this.setMintPrice}       
      BtnTxt={'修改'}
      />

      <OneInputOneBtnComponent
      Titel={'設定一次Mint數量上限'}
      Status={'現在數量上限'+this.state.MAX_Mint}
      Input1_Descript={'輸入Mint上限'}
      Input1_Type={'text'}
      Input1_Name={'setMax_Mint'}        
      Input1_ChangeFun={this.handleInputChange}
      ClickFun={this.setMaxBalance}       
      BtnTxt={'修改'}
      />

      <OneInputOneBtnComponent
      Titel={'設定持有NFT數量上限'}
      Status={'現在數量上限'+this.state.Max_Balance}
      Input1_Descript={'輸入NFT數量上限'}
      Input1_Type={'text'}
      Input1_Name={'setMaxBalanceQuantity'}        
      Input1_ChangeFun={this.handleInputChange}
      ClickFun={this.setMaxMint}       
      BtnTxt={'修改'}
      />

      <TwoInputOneBtnComponent
      Titel={'查詢持有數量'}        
      Input1_Descript={'輸入查詢地址'}
      Input1_Type={'text'}
      Input1_Name={'owneraccount'}        
      Input1_ChangeFun={this.handleInputChange}
      Input2_Descript={'持有數量：'+this.state.balanceQuantity}
      Input2_Type={'hidden'}        
      ClickFun={this.OwnerbalanceOf}       
      BtnTxt={'查詢'}
      />
      <OneBtnComponent
      Titel={'盲盒開啟/關閉'}
      SoldPrice={'現在狀態：'+this.state.isRevealed}
      ClickFun={()=>this.flipReveal()}
      Descript={'盲盒開啟/關閉'}
      BtnTxt={'開啟/關閉'}
      />
      <OneBtnComponent
      Titel={'銷售開啟/關閉'}
      SoldPrice={'現在狀態：'+this.state.isSaleActive}
      ClickFun={()=>this.flipSaleActive()}
      Descript={'銷售開啟/關閉'}
      BtnTxt={'開啟/關閉'}
      />
      <OneBtnComponent
      Titel={'金錢轉出'}
      SoldPrice={'金錢轉出'}
      ClickFun={()=>this.withdraw()}
      Descript={'金錢轉出'}
      BtnTxt={'轉出'}
      />  
    </div>
  </div>    
    }
    

    
  </div>  

 
  </div>  
</div>
);
}
}
export default App;