import React, { Component } from 'react'

export class ConnectInfoComponent extends Component {    
  render() {
    return (
        <div className='container'>
        <div className="row justify-content-center">
          <div className="col-md-8">
       <p className=''>Address: {this.props.Account}</p>
       <button
       className='btn btn-secondary mt-3 mt-md-4 px-4' 
       onClick={this.props.ClickFun} >Connect to MetaMask</button>
       </div>       
       <div className="col-md-4">
       
       </div>
       </div>
       </div>
    )
  }
}

export default ConnectInfoComponent
