import React, { Component } from 'react'

export class TwoInputOneBtnComponent extends Component {
  render() {
    return (
        <div className="w3-border w3-margin-bottom">        
        <div className="w3-container w3-white">
          <p><b>{this.props.Titel}</b></p>
          <p className="w3-opacity">{this.props.Status}</p>
          <p>{this.props.Input1_Descript}</p>          
          <input 
          className='w3-input'
          type={this.props.Input1_Type}
          name={this.props.Input1_Name}            
          onChange={this.props.Input1_ChangeFun} 
          />
          <p>{this.props.Input2_Descript}</p>
          <input 
          className='w3-panel w3-round-large'
          type={this.props.Input2_Type}
          name={this.props.Input2_Name}            
          onChange={this.props.Input2_ChangeFun} 
          />
          <br/>
          <button 
          className="w3-button w3-black w3-margin-bottom"
          onClick={this.props.ClickFun}
          >{this.props.BtnTxt}</button>
        </div>
      </div>
    )
  }
}

export default TwoInputOneBtnComponent
