import React, { Component } from 'react'

export class SoldInfoComponent extends Component {
  render() {
    return (
        <ul className="w3-ul w3-border w3-white w3-text-grey">
        <li className="w3-padding w3-xxxlarge">發行總數 <span className="w3-tag w3-teal w3-xxxlarge w3-margin-left">{this.props.MaxSupply}</span></li>
        <li className="w3-padding w3-xxxlarge">賣出數量 <span className="w3-tag w3-blue w3-xxxlarge w3-margin-left">{this.props.SoldOutAmount}</span></li>
        <li className="w3-padding w3-xxxlarge">剩餘數量 <span className="w3-tag w3-red w3-xxxlarge w3-margin-left">{this.props.RemainAmount}</span></li>
      </ul>
    )
  }
}

export default SoldInfoComponent
