export async function balanceOf(parameter){
  const nft=CallContract();
  const owner=parameter.owner
  const web3=window.web3;   
  if(!web3.utils.isAddress(owner)){
    return alert("System Error")
  }   
  let result=await nft.methods.balanceOf(owner).call()
  return result;
}

