export async function setMintPrice(parameter){
  const nft=CallContract();
  const accountAddress=parameter.account;
  const price=window.web3.utils.toWei(parameter.price);    
  const gasfee=parameter.gasfee;
  const gaslimit=parameter.gaslimt;
  await nft.methods.setMintPrice(price).send({
    from: accountAddress,
    gasPrice:gaslimit,      
    gas:gasfee      
  })
}

export function mintShow(account){
  return account!==''&&account!==undefined&&account.length>0
}

export function controlShow(account){
  const accountresult=account===''||account===undefined?'0x0':account.toUpperCase();
  return accountresult.toUpperCase()===Nft.Walletaddress.toUpperCase()
}

